:root {
  --font-family: "Inter", sans-serif;
  --second-family: "Raleway", sans-serif;
  --content-width: 1320px;
  --container-offset: 60px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
}

