*,
*::before,
*::after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  padding: 0px;
  margin: 0px;
  border: none;
  color: #000;
}

a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  padding: 27px 0 40px;
  height: 145px;
  background: right 0%/75% no-repeat url(/img/header/header-print.png);
}
.header__inner {
  position: relative;
  display: flex;
  justify-content: space-between;
}
.header__links {
  position: absolute;
  right: 0;
  top: -17px;
  display: flex;
  align-self: start;
  gap: 37px;
}
.header__link {
  display: flex;
  align-items: center;
  gap: 16px;
}
.header__svg {
  width: 30px;
  height: 30px;
}
.header__text {
  font-weight: 400;
  font-size: 20px;
  color: #fff;
}

.logo {
  display: flex;
  align-items: center;
  gap: 8px;
}

.title__header {
  font-family: var(--second-family);
  font-weight: 500;
  line-height: 110%;
  text-transform: uppercase;
}
.title__header-small {
  font-size: 28px;
}
.title__header-big {
  font-size: 39px;
}

.nav {
  display: flex;
  align-self: end;
  font-weight: 400;
  font-size: 20px;
}
.nav__list {
  display: flex;
  align-items: center;
  gap: 60px;
}
.nav__item-blue {
  padding: 8px 15px;
  border-radius: 8px;
  background-color: #00A4D7;
  transition: transform 0.3s ease-in-out;
}
.nav__link {
  position: relative;
  color: #000;
}
.nav__link:hover {
  color: #fff;
}
.nav__link:hover::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: -1;
  display: block;
  width: 130%;
  height: 130%;
  background-color: #00A4D7;
  border-radius: 5px;
  transform: translate(-50%, -50%);
}
.nav__link-white {
  color: #fff;
}
.nav__link-white:hover::before {
  content: "";
  display: block;
  width: 0;
  height: 0;
}
.nav__item-blue:hover {
  transform: scale(1.1);
}

.intro {
  background: url(/img/intro/intro-bg.jpg) center center/cover no-repeat;
}
.intro__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 200px 0 100px;
}
.intro__title {
  margin-bottom: 62px;
  font-family: var(--second-family);
  font-weight: 800;
  font-size: 113px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.intro__subtitle {
  margin-bottom: 43px;
  font-weight: 400;
  font-size: 47px;
  text-align: center;
  color: #fff;
}
.intro__usp {
  margin-bottom: 83px;
  font-weight: 400;
  font-size: 47px;
  color: #fff;
}
.intro__button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  border-radius: 20px;
  width: 513px;
  height: 89px;
  background-color: #3d2a79;
  font-weight: 600;
  font-size: 26px;
  color: #fff;
  transition: transform 0.3s ease-in-out;
}
.intro__button:hover {
  transform: scale(1.1);
}
.intro__svg {
  width: 30px;
  height: 30px;
}

.about {
  position: relative;
  padding: 130px 0 115px;
}
.about__inner {
  display: flex;
}
.about__rectangle {
  position: absolute;
  left: 0;
  top: 100px;
  z-index: -100;
  width: 70%;
  height: 91px;
  background: linear-gradient(90deg, #80B53D -15.04%, #00A4D7 103.48%);
  transform: matrix(-1, 0, 0, 1, 0, 0);
}
.about__rectangle::after {
  content: "";
  position: absolute;
  margin: -20px;
  width: 60px;
  height: 120px;
  transform: rotate(25deg);
  background-color: #fff;
}
.about__title {
  margin-bottom: 28px;
  font-weight: 400;
  font-size: 30px;
  color: #fff;
}
.about__subtitle {
  margin-bottom: 30px;
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 86px;
  text-transform: uppercase;
}
.about__text {
  max-width: 980px;
  font-weight: 400;
  font-size: 20px;
}

.statistics {
  padding: 95px 0;
  background: url(/img/statistics/statistics-bg.jpg) center center/cover no-repeat;
}
.statistics__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
}
.statistics__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
  font-weight: 400;
  font-size: 54px;
