}
.team__title {
  margin-left: 450px;
  align-self: center;
  justify-content: end;
  font-weight: 400;
  font-size: 30px;
  color: #fff;
}
.team__name {
  margin-bottom: 45px;
  font-weight: 400;
  font-size: 79px;
}
.team__description {
  margin-bottom: 40px;
  font-weight: 400;
  font-size: 49px;
}
.team__experience {
  font-weight: 400;
  font-size: 61px;
}

.team__pagination {
